import { Injectable } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import { AngularFireStorage } from '@angular/fire/storage';
import { auth } from 'firebase/app';
import { Router } from '@angular/router';
import swale from 'sweetalert';
import { Observable } from 'rxjs';
import { finalize } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class AuthService {
  imagenUrl: Observable<string>;
  identificador:string;
  constructor(private bdAuth:AngularFireAuth, private bdFire:AngularFirestore ,private navegar:Router, private alamacenar: AngularFireStorage) { }

  login(correo,clave){
    return new Promise((resolve, rejected)=> {
      this.bdAuth.signInWithEmailAndPassword(correo,clave).then(datos => {
        this.navegar.navigate(['/panel']);
        resolve(datos);
      }).catch(error => {
        swale('Error al Iniciar Sesión',error.code,'error');
        rejected(error);
      });
    });
  }

  registro(nombre, apellido, correo, clave){
    return new Promise((resolve, rejected) => {
      this.bdAuth.createUserWithEmailAndPassword(correo,clave).then(datos => {
        this.bdFire.collection('usuarios').doc(datos.user.uid).set({
          'nombre': nombre,
          'apellido':apellido,
          'correo':correo,
          'foto': '',
          'id': datos.user.uid
        });
        this.navegar.navigate(['/panel']);
        resolve(datos);
      }).catch(err=> {
        swale('Error al crear cuenta',err.code,'error');
        rejected(err);
      }); 
    });
  }

  google(){
    return new Promise((resolve, rejected)=>{
      this.bdAuth.signInWithPopup(new auth.GoogleAuthProvider()).then(datos => {
        this.bdFire.collection('usuarios').doc(datos.user.uid).valueChanges().subscribe(existe => {
          if(existe != null){
          }else{
            const usuario = datos.user.displayName;
            const nombre = usuario.substr(0,(usuario.indexOf(' ')));
            const longitud = nombre.length;
            const apellido = usuario.substr(longitud + 1);
            this.bdFire.collection('usuarios').doc(datos.user.uid).set({
              'nombre': nombre,
              'apellido':apellido,
              'correo':datos.user.email,
              'foto': '',
              'id': datos.user.uid
            });
          }
        });
        
        this.navegar.navigate(['/panel']);
        resolve(datos);
      }).catch(err => {
        swale('Error al Iniciar Sesión',err.code,'error');
        rejected(err);
      });
    });
  }

  facebook(){
    return new Promise((resolve, rejected)=>{
      this.bdAuth.signInWithPopup(new auth.FacebookAuthProvider()).then(datos => {
        this.bdFire.collection('usuarios').doc(datos.user.uid).valueChanges().subscribe(existe => {
          if(existe != null){
          }else{
            const usuario = datos.user.displayName;
            const nombre = usuario.substr(0,(usuario.indexOf(' ')));
            const longitud = nombre.length;
            const apellido = usuario.substr(longitud + 1);
            this.bdFire.collection('usuarios').doc(datos.user.uid).set({
              'nombre': nombre,
              'apellido':apellido,
              'correo':datos.user.email,
              'foto': '',
              'id': datos.user.uid
            });
          }
        });
        this.navegar.navigate(['/panel']);
        resolve(datos);
      }).catch(err => {
        swale('Error al Iniciar Sesión',err.code,'error');
        console.log(err.message);
        rejected(err);
      });
    });
  }
  datosUsuario(){
    return this.bdAuth.authState;
  }
    

  usuarioLogueado(id){
    return this.bdFire.collection('usuarios').doc(id).valueChanges();
  }

  actualizarPerfil(img, usuario, opcion){
    if(opcion == true){
      this.identificador = this.aleatroio(16);
      const archivo = img.target.files[0];
      const ubicacionArchivo = `fotosPerfil/${this.identificador}`;
      const ruta = this.alamacenar.ref(ubicacionArchivo);
      const subida = this.alamacenar.upload(ubicacionArchivo, archivo);
  
     return new Promise((resolve, rejected)=>{
      subida.snapshotChanges()
      .pipe(
        finalize(() => {
          ruta.getDownloadURL().subscribe(url => {
            this.imagenUrl = url;
            this.bdFire.collection('usuarios').doc(usuario.id).update({
              nombre: usuario.nombre,
              apellido:usuario.apellido,
              foto: url,
            }).then(res => resolve(res)).catch(err => rejected(err));
          });
         })
         ).subscribe();
     });
    
    }else{
      return new Promise((resolve, rejected) => {
        this.bdFire.collection('usuarios').doc(usuario.id).update({
          nombre: usuario.nombre,
          apellido:usuario.apellido,
        }).then(ok => {resolve(ok)}).catch(err => { rejected(err)});
      });
     
    }
  }

  aleatroio(length) {
    var result           = '';
    var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for ( var i = 0; i < length; i++ ) {
       result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
 }

  cerrarSesion(){
    this.bdAuth.signOut().then(ok => {

      this.navegar.navigate(['/login']);
    }
    ).catch(err => {
      console.log(err);
    });
    
  }
}
