import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AuthService } from '../../servicios/auth.service';
import swale from 'sweetalert';
@Component({
  selector: 'app-perfil',
  templateUrl: './perfil.component.html',
  styleUrls: ['./perfil.component.css']
})
export class PerfilComponent implements OnInit {
  validacionForm:FormGroup;
  usuario:any = [];
  foto:any = [];
  subioFoto:boolean = false;
  constructor(private builder: FormBuilder, private servicio: AuthService) {
    this.servicio.datosUsuario().subscribe(datos => {
      this.servicio.usuarioLogueado(datos.uid).subscribe(usu => {
        this.usuario = usu;
      });
    });
    
   }

  ngOnInit() {
    
  }

  consultarDatos(id){
    this.servicio.usuarioLogueado(id).subscribe(usu => {
      this.usuario = usu;
    })
  }

  validarFoto(e){
    this.foto = e;
    this.subioFoto = true;
    console.log(this.foto);
  }

  actualizarInfo(){
    if(this.usuario.nombre == "" || this.usuario.apellido == ""){
      swale('Error','La información proporcionada es incorrecta. Por favor complete los datos.','error');
    }
    console.log(this.foto,this.usuario,this.subioFoto);
      this.servicio.actualizarPerfil(this.foto,this.usuario,this.subioFoto).then(ok => {
        swale('Exito','Información actualizada','success');
      }).catch(err=>{
        swale('Falló al subir foto',err.code,'error');
      });
  }

}
