import { Component, OnInit } from '@angular/core';
import { TaskService } from '../../servicios/task.service';
@Component({
  selector: 'app-rest',
  templateUrl: './rest.component.html',
  styleUrls: ['./rest.component.css']
})
export class RestComponent implements OnInit {
  ide:string;
  constructor(private servicioTask:TaskService) { }

  ngOnInit(): void {
  }

  getAllTask(){
    this.servicioTask.getAllTask().subscribe(todos => {
      console.log(todos);
    });
  }

  getTask(){
    this.servicioTask.getTask(this.ide).subscribe(uno => {
      console.log(uno);
    });
  }

  createTask(){
    const task = {
      userId: "1364343436",
      id: "344646464",
      title: "Hola Munbdo",
      completed: true 
    };

    this.servicioTask.createTask(task).subscribe((crear) => {
        console.log(crear);
      });
  }

  updateTask(){
    const task = {
      userId: "1364343436",
      id: "190",
      title: "Hola Mundo",
      completed: true 
    };
    this.servicioTask.updateTask(task).subscribe(actualiza => {
      console.log(actualiza);
    });
  }

  deleteTask(){
    this.servicioTask.deleteTask(this.ide).subscribe(elimina => {
      console.log(elimina);
    });
  }

}
