import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { Routes , RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ReactivosComponent } from './componentes/reactivos/reactivos.component';
import { LoginComponent } from './componentes/login/login.component';
import { firebaseConfig } from '../environments/environment';
import { AngularFireModule } from '@angular/fire';
import { AngularFireAuthModule } from '@angular/fire/auth';
import { AngularFirestoreModule } from '@angular/fire/firestore';
//REST API
import { HttpClientModule } from '@angular/common/http';
import { RestComponent } from './componentes/rest/rest.component';
import { NavbarComponent } from './componentes/navbar/navbar.component';
import { FooterComponent } from './componentes/footer/footer.component';
import { PanelComponent } from './componentes/panel/panel.component';
import { LoginGuard } from './guards/login.guard';
import { PanelGuard } from './guards/panel.guard';
import { PerfilComponent } from './componentes/perfil/perfil.component';
const rutas:Routes = [
  {path:'login', component:LoginComponent, canActivate:[LoginGuard]},
  {path:'registro', component:ReactivosComponent, canActivate:[LoginGuard]},
  {path:'panel', component:PanelComponent, canActivate:[PanelGuard]},
  {path:'perfil', component:PerfilComponent, canActivate:[PanelGuard]},
  {path:'rest', component:RestComponent},
  {path:'**', component:LoginComponent, canActivate:[LoginGuard]},
];
@NgModule({
  declarations: [
    AppComponent,
    ReactivosComponent,
    LoginComponent,
    RestComponent,
    NavbarComponent,
    FooterComponent,
    PanelComponent,
    PerfilComponent,
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(rutas),
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireAuthModule,
    AngularFirestoreModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
